.tray {
    width: 45%;
    height: 80px;
    /* height: 50px; */
    border: 3px solid var(--primaryColor);
    display: flex;
    flex-direction: row;
    border-radius: 0.2rem;
}

.holder {
    width: 20%;
    border: 2px solid var(--primaryColor);
    transition: all 0.3s;
    word-wrap: break-word;
}

.holder:hover {
    cursor: pointer;
    background-color: var(--gray);
}
.modal {
    transform: translate(35vw,30vh);
    width: 300px;
    height: 200px;
    padding-top: 20px;
    border-radius: 1rem;
}

.modal > .modal-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    
  }