.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.sampleLoader {
  width: 800px;
  height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 5px solid var(--gray);
  border-radius: 1rem;
  flex-wrap: wrap;
  padding: 20px;
  gap: 20px;
}

.buttons {
  display: flex;
  flex-direction: row;
  gap: 20px;
}

.buttons > i {
  color: var(--primaryColor);
  transition: all 0.2s;
}

.buttons > i:hover {
  cursor: pointer;
  color: var(--gray);
}